body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;  
}
.navbar{
  display: flex;
  background-color: transparent;
  position: -webkit-sticky;
  position: sticky;
  z-index: 999;
}
.nav-img  {
  display: flex;
  justify-content: start;
  text-align: center;
  position: relative;
  margin-left: 4.5rem;
  top: 1.3rem;
  height: 50px;
}
.header{
  height:5.5rem;
  width: 100%;
  top: 0;
  font-size: larger;
  display: flex;
  justify-content: center;
  cursor: pointer;
}
.navbar.active{
  background-color:#f6b01bfb;
  height: 80px;
  top: 0;
  box-shadow: 0 1.25rem 2.1875rem 0 rgb(241 165 1 / 15%);
}
.header li {
  margin: 0px 20px;
  padding: 10px;
}
.header li Link{
  font-size: 25px;
  font-weight: 700px;
  color: black;
  text-decoration: none;
  text-transform: capitalize;
}
 .header li:hover{
  border-bottom: 4px solid red;
  border-radius: 2.5px;
}
.nav-menu{
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap-reverse;
  list-style: none;
  transition: .5s; 
  align-items: center;
}
 .signup{
  font-size: 17px;
  border: 1px solid;
  border-radius: 0.3rem;
  width:85px;
  height: 30px;
  color: black;
  margin-top: 10px;
  text-transform: capitalize;
  position: relative;
  margin-left: 5rem;
  display: flex;
  justify-content: center;
  text-decoration: none;
  border-color:black;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  padding-top: 4.5px;
}
.signup:hover{
  background-color:black;
  color: white;
}
.nav-bar{
  display: none;
}

/* Banner Section */
.bannercontainer{
  margin-bottom: 5rem;
  display: flex;
  flex-direction: row-reverse;
  justify-content: start;
  justify-content: space-between;
  margin: 7rem 7rem;
}
.bimg{
    display: flex;
    position: relative;
    bottom: 2.8rem;
    height: 100vh;
    z-index: -1;
}
.banner-des{
    z-index: -1;
    position: relative;
}
.banner-des h4{
    color: #ee3713 ;
    font-family: 'Roboto', sans-serif ;
    margin-bottom: .5rem;
    font-weight: 700;
    font-size: 3.5vmin;
    z-index: -1;
}
.banner-des h1{
    font-family: "Volkhov","DM Serif Display";
    font-weight: 700;
    font-size: 12.8vmin;
    color: #181E4B;
    margin-bottom: 5px;
    position: relative;
    z-index: -1;
    display: flex;
}
.banner-des p{
    font-size: 1rem;
    margin-bottom: 3rem;
    z-index: -1;
}
.button{
  display: flex;
  justify-content: space-between;
}
.button .btn{
  text-decoration: none;
  background-color: #F1A501;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  color: white;
  border-radius: 0.625rem;
  padding: .8rem 1rem;
  font-size: 1.125rem;
  margin-bottom: 5rem;
  box-shadow: 0 1.25rem 2.1875rem 0 rgb(241 165 1 / 15%);
  z-index: -1;
}
.round-circle{
color: #0b0b10d5;
display: flex;
position: relative;
right: 13rem;
justify-content: start;
flex-direction: row-reverse;
font-size: 20px;
font-weight: 600;
cursor: pointer;
z-index: -1;
}
.round-circle img{
   right: .8rem;
   bottom:.7rem;
  display: flex;
  justify-content: start;
   width: 4rem;
  height: 4rem;
 position: relative;
 cursor: pointer;
 z-index: -1;
 } 

/* About Section */
.about{
  margin-top: 5rem;
  
}
.aboutimg{
  display: flex;
  padding: 5rem 5rem;
  justify-content: space-between;
}
.about h1{
  margin-top: 5rem;
  font-family: "Volkhov","DM Serif Display";
  font-weight: 700;
  font-size: 10vmin;
  color: #181E4B;
  display: flex;
  justify-content: center;
  position: relative;
}
.about img{
  width: 85vmin;
  height: 66vmin;
  border-radius: 2rem;
  z-index: -1;
  box-shadow:  0 1.25rem 2.1875rem 0 rgb(241 165 1 / 15%);
}
.aboutdes h2 {
  position: relative;
 display: flex;
 justify-content: start;
  font-size: 7vmin;
  font-family: cursive;
  font: italic;
  font-weight: 700;
  color: #181E4B;
  margin-bottom: 2rem;
}
.aboutdes p{
  position: relative;
  display: flex;
 font-size: 2.5vmin;
}
.aboutbtn{
display: flex;
}
.more{
  margin-top: 2rem;
  text-decoration: none;
  background-color: #F1A501;
  color: white;
  border-radius: 0.625rem;
  width: 8rem;
  padding: .8rem 1rem;
  font-size: 1.125rem;
  position: relative;
  box-shadow: 0 1.25rem 2.1875rem 0 rgb(241 165 1 / 15%);
}

 /* card section */
 .heading{
  display: flex;
  justify-content: center;
 margin-bottom: 3.5rem;
 margin-top: 5rem;
 font-family: "Volkhov","DM Serif Display";
 font-weight: 700;
 font-size: 3.24732rem;
 color: #181E4B;
}
.cardContainer{
  display: inline-block;
  justify-content: center;
  width: 25rem;
  height: 40rem;
  margin-left: 5rem;
}
.cardbody{
  max-width: 25rem;
  max-height: 40rem;
  box-shadow:  0 1.25rem 2.1875rem 0 rgb(241 165 1 / 15%);
  border-bottom-left-radius: 1.5rem;
  border-bottom-right-radius: 1.5rem;
  border-top-left-radius: 1.5rem;
  border-top-right-radius: 1.5rem;
  margin-bottom: .5rem;
}
.cardbody:hover{
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  -webkit-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);
}
.cardimage{
  width: 25rem;
  border-top-left-radius: 1.5rem;
  border-top-right-radius: 1.5rem;
}
.cardname{
  font-size: 1.5rem;
  margin-left: .8rem;
  margin-right: .5rem;
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
}
.carddes{
 margin-left: .8rem;
 padding-bottom: 15px;
}

/* Bookings */
.booking{
  margin: 3.8rem 8rem;
  display: flex;
}
.bookingdes h1{
  font-family: "Volkhov","DM Serif Display";
  font-weight: 700;
  font-size: 9.2vmin;
  color: #181E4B;
  margin-bottom: 5px;
  position: relative;
  z-index: -1;
  display: flex;
}
.bookingdes h5{
 
  font-size: 3.5vmin;
  color: #5E6282 ;
} 
.bookingdes1{
  margin-bottom: 10rem;
  display: flex;
  justify-content: end;
}
.bookingdes1 h5{
  font-size: 3vmin;
  position: relative;
  margin-left: 80px;
  top: 50px;
}
.bookingdes1 p{  
  font-size:2.5vmin;  
  margin-left:  80px ;
}
.imgg{
height: 50px;
position: relative;
top: 30px;
}
.bookingbanner img{
  position: relative;
  display: flex;
  margin-left: 18rem;
  justify-content: space-between;
  border-radius: 50px;
  box-shadow:  0 1.25rem 2.1875rem 0 rgb(241 165 1 / 15%);
}
/* services */
.servicedes{
  margin-bottom: 10rem;
}
.servicedes h1{
 position: relative;
 font-family: "Volkhov","DM Serif Display";
 font-weight: 700;
 font-size: 4.24732rem;
 color: #181E4B;
 margin-bottom: 8rem;
 z-index: -1;
 display: flex;
 justify-content: center;
}
.servicedes h5{
  font-size: 1.525rem;
  color: #5E6282 ;
  display: flex;
  justify-content: center;
}
.cardrow{
  display: flex;
  margin-bottom: 5rem;
}
.cardcol{
  border-radius: 25px;
  box-shadow:  0 1.25rem 2.1875rem 0 rgb(241 165 1 / 15%);
  height: 16rem;
  width: 18rem;
  margin: 0rem 3rem;
}
.cardcol img{
  display: flex;
  justify-content: center;
  margin: 1rem 6rem;
}
.cardcol h5{
  font-size: 1.525rem;
  color: #5E6282 ;
  margin-top:1rem;
  display: flex;
  justify-content: center;
  align-items: center; 
}
.cardcol p{
  display: flex;
  justify-content: center;
  font-size: 18px;
  font-weight: 500;
  color:#5E6282;
  margin-top: 15px;
  margin-bottom: 10px;
}
.serimg{
  height: 5rem;
}

/* Newsletter */
.newscontainer{
  background-color: rgba(223, 215, 249, 0.199);
  border-radius: 129px 20px 20px 20px;
  margin: .8rem 7rem;
  margin-bottom: 5rem;
  height: 25rem;
}
.newsletter{
  display: flex;
  justify-content: center;
}
.newsletter h2{
  display: flex;
  justify-content: center;
  color:#5E6282;
  margin-top: 6rem;
  font-weight: 700;
  font-size: 4.5vmin;
}
.form{
  display: flex;
  justify-content: center;
  margin-top: 2.8rem;
  text-decoration: none;
  border: none;
}
.form1{
  margin: 2rem;
  color: #39425D;
  padding: 1rem 19rem 1rem 2.7rem;
  text-decoration: none;
   font-weight: 500;
   font-size: 1rem;
   display: flex;
   justify-content: center;
    background-clip: padding-box;
    background-color: #ffffff;
    border-radius: 10px;
    align-items: center;
    border: none;
    box-shadow: 0 .25rem .1875rem 0 rgb(241 165 1 / 15%);
}
.newsbtn{
  display: flex;
  margin-top: 2rem;
  justify-content: center;
  text-decoration: none;
  background-color: #ff946d;
  color: white;
  border-radius: 0.625rem;
  width: 8rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: .8rem 1rem;
  font-size: 1.125rem;
  box-shadow: 0 1.25rem 2.1875rem 0 rgb(241 165 1 / 15%);
}

/* Footer */
.footer{
  background-color: rgba(220, 213, 213, 0.145);

 }
 .row{
   
   display: flex;
   justify-content: start;
   padding: .8rem 2rem; 
 }
 .col{
   padding: 2rem;
   margin: 1rem .5rem;
 }
 .colimg{
   height: 3rem;
 }
 .col p{
   font-size: 1.2rem;
   margin-top: 1rem;
   color: #5E6282;
 }
 .col h4{
   font-size: 1.5rem;
   font-weight: 700;
 }
 .col li{
   padding: .1rem;
   list-style: none;
   color: #5E6282;
   font-size: 1.2rem;
   margin-top: 1rem;
   cursor: pointer;
 }
 .right {
  display: flex;
  justify-content: center;
   width: 100%;
   color: #5E6282; 
 }

@media screen and (max-width:900px) {
